.services{
  padding: 0 3rem 0 3rem;
  display: flex;
  height: 90vh;

}
.awesome{
  display: flex;
  flex-direction: column;
  position: relative;
}
.awesome>:nth-child(1){
  color:var(--black);
  font-size: 2.5rem;
  font-weight: bold;
}

.awesome>:nth-child(2){
  color:var(--gray);
  font-size: 22px;
  margin-top: 1rem;
   width: 32rem; 
}
.s-button{
  width: 8rem;
  height: 2rem;
  margin-top: 1rem;

}
.cards{
  position: relative;
}
.cards>*{
  position: absolute;
}