
.container{
  width: 90%;
  padding-right: 1.25rem;
  padding-left: 1.25rem;
  padding-top: 2rem;
  margin-right: auto;
  margin-left: auto;
}
@media (min-width:48rem) {
  .container{
    margin: 0 auto;
  }
}
@media(min-width:62rem){
  .container{
    max-width: 60rem;
  }
}
@media(min-width:75rem){
  .container{
    max-width: 71.25rem;
  }
}
@media(min-width:87.5rem){
  .container{
    max-width: 84.37rem;
    margin-top: -8rem;
  }
}

.skill_title h2{

  font-size: 3rem;
  line-height: 1.5;
  color: black;
  text-align: center;
  position: relative;
    padding: 4rem;
    bottom:12rem;
}
.skill-section{
  padding: 13.43rem 0.25rem;
  background-color: rgb(222, 227, 212);
  position: relative;
}
.skill-section .skill_items{
  margin-top: -17rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  -webkit-box-pack: justify;
  -ms-flex-pack: justify;
  justify-content: space-between;
  gap: 2rem;

}
.skill_item{
  -webkit-box-flex: 0;
  -ms-flex: 0 0 48%;
  flex: 0 0 48%;
  max-width: 34%;
}
.skill_info{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
  -ms-flex-pack: justify;
  justify-content: space-between;
}
.skill_info p{
  font-weight: 600;
  font-size: 1.3rem;
    line-height: -1rem;
  color: black;
  -webkit-animation: showText 0.5s 1s linear forwards;
  animation: showText 0.5s 1s linear forwards;
}
.progress_line{
  margin-top: .7rem;
    height: 0.9rem;
    width: 95%;
    background-color: rgb(198, 198, 230);
    border-radius: 20px;
}
.progress_line span{
  -webkit-transform: scaleX(0);
  transform: scaleX(0);
  -webkit-transform-origin:left;
  transform-origin: left;
  -webkit-animation: animate 1s cubic-bezier(1,0,0.5,1) forwards;
  animation:animate 1s cubic-bezier(1,0,0.3,1) forwards; 
  height: 2%;
  max-width: 14rem;
  border-radius: 50px;
  position: absolute;
  background-color: rgb(0, 0, 255);
transition: 1s all;
}
.progress_line span.animate{
  -webkit-animation: animate 1.5s 0.5s cubic-beizer(1,0,0.5,1) forwords;
  animation: animate 1.5s 0.5s cubic-beizer(1,0,0.5,1) forwards;
}
@-webkit-keyframes showText{
  100%{
    opacity:1
  }
}
@keyframes showText{
  100%{
    opacity:1
  }
}
@-webkit-keyframes animate{
  100%{
    -webkit-transform: scaleX(1);
    transform: scaleX(1);
  }
}
@keyframes animate{
  100%{
    -webkit-transform: scaleX(1);
    transform: scaleX(1);
  }
}